.footer {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
}
.footer-text {
  text-align: center;
  color: #939393;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.25;
  margin-bottom: 19px;
  @media screen and (min-width: 320px) {
    font-size: 14px;
    line-height: 1.21;
    flex-direction: column;
    margin-left: 33px;
    margin-right: 33px;
    margin-bottom: 27px;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 18px;
  }
}

.heart {
  font-size: 16px;
  line-height: 1.25;
  color: rgb(247, 95, 68);
}


p {
  padding: 0;
  margin: 0;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-top: 0;
  padding-bottom: 0;
  color: #545454;

  .footer__image {
    padding-left: 10px;
    padding-right: 10px;
  }

  @media screen and (min-width: 320px) {
    width: 320px;
    padding-left: 20px;
    padding-right: 20px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding-left: 75px;
    padding-right: 75px;
  }

  @media screen and (min-width: 1024px) {
    width: 1024px;
    padding-left: 71px;
    padding-right: 71px;
  }
}



@media screen and (min-width: 320px) {
  .copyright {
    margin: 0px;
    display: flex;
    justify-content: center;
    padding-top: 29px;
    padding-bottom: 10px;
  }

  .developers {
    margin: 0px;
    display: flex;
    justify-content: center;
    padding-bottom: 30px;
  }
  .page__footer {
    font-size: 14px;
    font-weight: 400;
  }
}

@media screen and (min-width: 768px) {
  .page__footer {
    display: flex;
    justify-content: center;
  }

  .copyright {
    padding-top: 30px;
    padding-bottom: 28px;
  }
  .developers {
    padding-top: 30px;
    padding-bottom: 28px;
  }

  .page__footer {
    font-size: 16px;
    font-weight: 400;
  }
}

.team-modal {
  text-decoration: underline;
  cursor: pointer;
}